import React, { useCallback, useRef, useState } from 'react';
import './App.scss';
// import * as dayjs from 'dayjs'
import dayjs from 'dayjs';
// import 'dayjs/locale/zh-cn' // import locale


// var dayjs = require('dayjs');
// var duration: any = require('dayjs/plugin/duration').
import duration from 'dayjs/plugin/duration';
import relativeTime from 'dayjs/plugin/relativeTime';
dayjs.extend(duration);
dayjs.extend(relativeTime)


interface ISentence {
  days: number,
  months: number,
  years: number
}

interface IState {
  numerator: number,
  denominator: number,
  sentence: ISentence
}

const initialState: IState = {
  numerator: 1,
  denominator: 6,
  sentence: {
    days: 20,
    months: 0,
    years: 0
  }
}

const App = () => {
  let reductionBodyRef = useRef(null);
  const [state, setState] = useState(initialState);
  const updateState = useCallback(e => setState(current => ({ ...current, [e.target.name]: e.target.value })), []);
  const updateSentence = useCallback(e => setState(current => ({ ...current, sentence: {
    ...current.sentence,
    [e.target.name]: parseInt(e.target.value) || 0
  }})), []);

  const focusOnReduction = useCallback(() => {
    if (reductionBodyRef.current) {
      (reductionBodyRef.current as any).focus();
      console.log(50, 'focus');
    }
  }, []);
  // const newSentence = dayjs.duration({
  //   days: state.sentence.days,
  //   months: state.sentence.months,
  //   years: state.sentence.years
  // });
  const totalDays = state.sentence.days + state.sentence.months*30 + state.sentence.years*365;
  const totalMonths = state.sentence.months + state.sentence.years*12;
  const newSentenceDays = Math.floor(totalDays*state.numerator/state.denominator);
  const newSentenceMonths = totalMonths*state.numerator/state.denominator;
  console.log(52, totalDays, newSentenceDays);
  // const newSentence = dayjs.duration(newSentenceDays, 'days');
  const newSentence = { years: 0, months: 0, days: 0 };
  // newSentence.years = Math.floor(newSentenceDays/365);
  // newSentence.months = Math.floor((newSentenceDays - newSentence.years*365)/30);
  // newSentence.days = newSentenceDays - newSentence.years*365 - newSentence.months*30;
  newSentence.years = Math.floor(newSentenceMonths/12);
  newSentence.months = Math.floor(newSentenceMonths) % 12;
  console.log(55, newSentenceMonths, Math.floor(newSentenceMonths));
  // newSentence.days = Math.floor((newSentenceMonths - Math.floor(newSentenceMonths))*30 + state.sentence.days);
  newSentence.days = Math.floor(((totalMonths*state.numerator % state.denominator)*30 + state.sentence.days)*state.numerator/state.denominator);
  console.log(58, newSentence);
  return (
    <div className="w-100 h-100 d-flex justify-content-center align-items-center">
      <div className="p-4 d-flex flex-column card">

        <div className="mb-2 card">
          <div className="card-header">
            Sentença
          </div>
          <form className="card-body">

            <div className="row">
              <label className="col-sm-5 col-form-label" htmlFor="sentence-days">Dias: </label>
              <div className="col-sm-4">
                <input name="days" type="number" min="0" max="31" size={2} maxLength={2}
                  aria-label="Dias da sentença original"
                  className="ml-1 form-control" id="sentence-days"
                  value={state.sentence.days} onChange={updateSentence} />
              </div>
            </div>

            <div className="row">
              <label className="col-sm-5 col-form-label" htmlFor="sentence-months">Meses: </label>
              <div className="col-sm-4">
                <input name="months" type="number" min="0" max="12" size={2} maxLength={2}
                  aria-label="Meses da sentença original"
                  className="ml-1 form-control" id="sentence-months"
                  value={state.sentence.months} onChange={updateSentence} />
              </div>
            </div>

            <div className="row">
              <label className="col-sm-5 col-form-label" htmlFor="sentence-years">Anos: </label>
              <div className="col-sm-4">
                <input name="years" type="number" min="0" size={3} maxLength={3}
                  aria-label="Anos da sentença original"
                  className="ml-1 form-control" id="sentence-years"
                  value={state.sentence.years} onChange={updateSentence} />
              </div>
            </div>

          </form>
        </div>

        <div className="mb-2 card">
          <div className="card-header">
            Fração
          </div>
          <form className="card-body">

            <div className="row">
              <label className="col-sm-7 col-form-label" htmlFor="fraction-numerator">Numerador: </label>
              <div className="col-sm-4">
                <input name="numerator" type="number" min="0" size={3} maxLength={3}
                  aria-label="Numerador da fração"
                  className="ml-1 form-control" id="fraction-numerator"
                  value={state.numerator} onChange={updateState} />
              </div>
            </div>

            <div className="row">
              <label className="col-sm-7 col-form-label" htmlFor="fraction-denominator">Denominador: </label>
              <div className="col-sm-4">
                <input name="denominator" type="number" min="0" size={3} maxLength={3}
                  aria-label="Denominador da fração"
                  className="ml-1 form-control" id="fraction-denominator"
                  value={state.denominator} onChange={updateState} />
              </div>
            </div>

          </form>
        </div>

        <button onClick={focusOnReduction}>Atualizar redução</button>

        <div className="mb-2 card">
          <div className="card-header">
            Redução de sentença:
          </div>

          <div className="card-body" >
            <textarea value={`Dias reduzidos: ${newSentence.days}`} ref={reductionBodyRef} readOnly/>
            <div className="row">
              <label className="col-sm-5 col-form-label">Dias: </label>
              <div className="col-sm-4">
                <input name="days" className="ml-1 form-control"
                  aria-label="Dias reduzidos"
                  value={newSentence.days} readOnly/>
                <span aria-label={`Dias reduzidos: ${newSentence.days}`}>Dias: {newSentence.days}</span>
              </div>
            </div>

            <div className="row">
              <label className="col-sm-5 col-form-label">Meses: </label>
              <div className="col-sm-4">
                <input name="months" className="ml-1 form-control"
                  aria-label="meses reduzidos"
                  value={newSentence.months} readOnly/>
              </div>
            </div>

            <div className="row">
              <label className="col-sm-5 col-form-label">Anos: </label>
              <div className="col-sm-4">
                <input name="years" className="ml-1 form-control"
                  aria-label="anos reduzidos"
                  value={newSentence.years} readOnly/>
              </div>
            </div>

          </div>
        </div>
        
      </div>
    </div>
  );
}

export default App;
